.nav {

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    position: relative;
    color: #1d1d1d;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &::after {
      content: '';
      position: absolute;
      display: block;
      bottom: -8px;
      height: 1px;
      width: 100%;
      background-color: #1d1d1d;

      transform: scale(0);
      transition: transform 0.3s;
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}

.book-test-ride {
  color: #1d1d1d;
  text-decoration: none;
  display: block;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  padding-bottom: 7px;
  border-bottom: 1px solid #1d1d1d;
  width: fit-content;
}
